}
.social-block__toggler::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #ffffff;
  z-index: -1;
  transition: background-color 0.3s ease, width 6s ease 1s, height 6s ease 1s;
