@keyframes up {
  to {
    transform: translateY(0%);
  }
}
.line {
  display: block;
  overflow: hidden;
  display: flex;
  flex-wrap: nowrap;
  padding-top: 2%;
}

.letter {
  display: inline-block;
  transform: translateY(203%);
  overflow: visible;
}
.letter--prepared {
  will-change: transform;
}
.letter--appeared {
  animation-name: up;
  animation-duration: 1.3s;
  animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  animation-direction: normal;
  animation-fill-mode: forwards;
}
.letter--appeared:nth-child(1n) {
  animation-delay: 200ms;
}
.letter--appeared:nth-child(2n) {
  animation-delay: 100ms;
}
.letter--appeared:nth-child(3n) {
  animation-delay: 0s;
}
.letter--appeared:nth-child(4n) {
  animation-delay: 100ms;
}
.letter--appeared:nth-child(5n) {
  animation-delay: 200ms;
}

